// new FormData() // 
export const updatePhotoAPI = (formObj) => {
  return axios({
    url: '/v1_0/user/photo',
    method: 'PATCH',
    data: formObj
    // , Content-Typemultipart/form-data
  })
}
//  - 
export const updateProfileAPI = (dataObj) => {
  const obj = {
    name: '',
    gender: 0,
    birthday: '',
    intro: ''
  }
  for (const prop in obj) { // key
    if (dataObj[prop] === undefined) { // key()
      delete obj[prop] // obj
    } else {
      obj[prop] = dataObj[prop] // keyobj
    }
  }
  return axios({
    url: '/v1_0/user/profile',
    method: 'PATCH',
    data: obj
  })
}
